.App {
  min-height: 100vh;
  max-width: 820px;
  padding: 0 24px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.AppHeader {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.AppLink {
  color: #61dafb;
}

.iframe{
  border: 0;
  width: 100%;
  height: 400px;
  flex: 1 0;
}

.iframeVisible{
  border: 2px gray dashed;
}

.invisible {
  visibility: hidden;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
